html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/*----------------------------------------------------CODE---------------------------------------------------*/

.container {
	margin: auto;
	background-image: url(/src/assets/images/background-image.jpg);
	max-width: 1360px;
}

header {
	margin: 0px 40px;
	padding-top: 30px; 
}

.hat {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 10px 40px;
	border-top: 2px solid rgb(75, 57, 57);
}

.hat2 {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	border-top: 2px solid rgb(75, 57, 57);
	border-bottom: 2px solid rgb(75, 57, 57);
	padding: 5px 40px;
}

.main-info {
	display: grid;
	grid-template-columns: 2fr 1fr 1fr;
	grid-gap: 20px;

&_block1 {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin: 20px 40px;
	}	
}

.duo-line {
	padding: 3px;
	border-top: 3px solid rgb(75, 57, 57);
	border-bottom: 2px solid rgb(75, 57, 57);
	margin-bottom: 20px;
}

.ed-news {
	text-align: center;
	margin-bottom: 10px;
}

.block1_part1 {
	text-align: center;
	word-break: break-all;

	&_auto {
	vertical-align: top;
	float: left;
	margin-right: 10px;
	}

	&_texthat {
		text-align: center;
		margin-bottom: 10px;
	}
}

.block1_part2 {
	display: flex;
	flex-direction: column;
	text-align: center;
	word-break: break-all;

	&_extra-news {
		margin-bottom: 10px;
	}

	&_star-icon {
		padding: 0px 10px;
	}

	&_world-tour {
		margin-bottom: 20px;
	}

	&_article {
		justify-content: space-between;
	}

	&_camera-pic {
		vertical-align: right;
		float: right;
		margin: 20px;
		border: 4px double rgb(75, 57, 57);
		outline: 2px solid rgb(75, 57, 57);
	}

	&_text {
		text-align: center;
	}	
}